<template>
    <v-container>
        <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
                <h3 class="display-1">Create Meetup</h3>
            </v-flex>
        </v-layout>
        <v-layout row>
            <v-flex xs12>
                <form @submit.prevent="onCreateMeetup">
                    <v-layout row>
                        <v-flex xs12 sm6 offset-sm3>
                            <v-text-field
                                name="title"
                                label="Title"
                                id="title"
                                v-model="title"
                                required
                            >
                            </v-text-field>
                        </v-flex>
                    </v-layout>
                    <v-layout row>
                        <v-flex xs12 sm6 offset-sm3>
                            <v-text-field
                                name="location"
                                label="Location"
                                id="location"
                                v-model="location"
                                required
                            >
                            </v-text-field>
                        </v-flex>
                    </v-layout>
                    <v-layout row>
                        <v-flex xs12 sm6 offset-sm3>
                            <v-btn raised dark class="cyan darken-2 ml-0" @click="onPickFile">Upload Image</v-btn>
                            <input type="file" style="display:none" ref="fileInput" accept="image/*" @change="onFilePicked">
                        </v-flex>
                    </v-layout>
                    <v-layout row>
                        <v-flex xs12 sm6 offset-sm3>
                            <img :src="imageUrl" height="150px">
                        </v-flex>
                    </v-layout>
                    <v-layout row>
                        <v-flex xs12 sm6 offset-sm3>
                            <v-textarea
                                name="description"
                                label="Description"
                                id="description"
                                v-model="description"
                                required
                            >
                            </v-textarea>
                        </v-flex>
                    </v-layout>
                    
                    <v-layout row>
                        <v-flex xs12 sm6 offset-sm3>
                            <h2>Pick Date & Time</h2>
                        </v-flex>
                    </v-layout>
                    
                    <v-layout row class="mb-2">
                        <v-flex xs12 sm6 offset-sm3>
                            <v-menu
                                ref="menu"
                                v-model="menu"
                                :close-on-content-click="false"
                                :nudge-right="40"
                                :return-value.sync="datepicker"
                                lazy
                                transition="scale-transition"
                                offset-y
                                full-width
                                min-width="290px"
                            >
                                <v-text-field
                                slot="activator"
                                v-model="datepicker"
                                label="Date"
                                prepend-icon="event"
                                readonly
                                ></v-text-field>
                                <v-date-picker v-model="datepicker" no-title scrollable>
                                <v-spacer></v-spacer>
                                <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                                <v-btn text color="primary" @click="$refs.menu.save(datepicker)">OK</v-btn>
                                </v-date-picker>
                            </v-menu>
                            <!-- <v-date-picker v-model="datepicker"></v-date-picker>
                            {{datepicker}} -->
                        </v-flex>
                    </v-layout>
                    <v-layout row>
                        <v-flex xs12 sm6 offset-sm3>
                            <v-menu
                                ref="menutime"
                                v-model="menutime"
                                :close-on-content-click="false"
                                :nudge-right="40"
                                :return-value.sync="timepicker"
                                lazy
                                transition="scale-transition"
                                offset-y
                                full-width
                                min-width="290px"
                            >
                                <v-text-field
                                slot="activator"
                                v-model="timepicker"
                                label="Time"
                                prepend-icon="access_time"
                                readonly
                                ></v-text-field>
                                <v-time-picker v-model="timepicker">
                                <v-spacer></v-spacer>
                                <v-btn text color="primary" @click="menutime = false">Cancel</v-btn>
                                <v-btn text color="primary" @click="$refs.menutime.save(timepicker)">OK</v-btn>
                                </v-time-picker>
                            </v-menu>
                            <!-- <v-time-picker v-model="timepicker" ></v-time-picker>
                            {{timepicker}} -->
                        </v-flex>
                    </v-layout>
                    <v-layout row>
                        <v-flex xs12 sm6 offset-sm3 text-xs-right>
                            <v-btn flat dark class="cyan darken-2" :disabled="!formIsvalid" type="submit">Create Meetup</v-btn>
                        </v-flex>
                    </v-layout>
                </form>
            </v-flex>
        </v-layout>
    </v-container>
</template>
<script>
export default {
    data(){
        return{
            title:"",
            location:"",
            imageUrl:"",
            description:"",
            timepicker:null,
            datepicker: null,
            menu: false,menutime:false,
            modal: false,
            image: null
        }
    },
    computed:{
        formIsvalid(){
            return this.title !== "" 
            && this.location !== ""
            && this.imageUrl !== ""
            && this.description !== ""
        },
        submittableDateTime(){
            const date = new Date(this.datepicker);
            if(typeof this.timepicker === 'string')
            {
                const hours = this.timepicker.match(/^(\d+)/)[1];
                const minutes = this.timepicker.match(/:(\d+)/)[1];
                date.setHours(hours);
                date.setMinutes(minutes);
            }
            else 
            {
                date.setHours(this.timepicker.getHours());
                date.setMinutes(this.timepicker.getMinutes());
            }
            return date;
        }
    },
    methods:{
        onCreateMeetup(){
            if(!this.formIsvalid)
            {
                return
            }
            const meetupData = {
                title : this.title,
                location : this.location,
                image : this.image,
                description : this.description,
                date: this.submittableDateTime
            }
            this.$store.dispatch("createMeetup",meetupData)
            this.$router.push('/meetups/')
        },
        onPickFile(){
            this.$refs.fileInput.click();
        },
        onFilePicked(event)
        {
            const files = event.target.files
            let filename = files[0].name
            if(filename.lastIndexOf('.')<=0)
            {
                return alert("please add a valid file")
            }
            const fileReader = new FileReader();
            fileReader.addEventListener("load",()=>{
                this.imageUrl = fileReader.result
            })
            fileReader.readAsDataURL(files[0])
            this.image = files[0]
        }
    }
}
</script>
