<template>
    <v-dialog
      max-width="400px"
      persistent
      v-model="editDialog"
    >
    <v-btn
      color="primary"
      dark
      class="cyan darken-2 ml-0"
      accent
      slot="activator"
    >
    Edit Time
    </v-btn>
    <v-card>
        <v-container>
            <v-layout row wrap>
                <v-flex xs12>
                    <v-card-title>
                        Edit Meetup Date
                    </v-card-title>
                </v-flex>
            </v-layout>
            <v-divider></v-divider>
            <v-layout row wrap>
                <v-flex xs12>
                    <v-time-picker v-model="editableTime" style="width:100%" actions>
                        <template>
                            <v-btn 
                                dark 
                                flat 
                                class="purple darken-4" 
                                @click.native="editDialog = false">
                                Close
                            </v-btn>
                            <v-btn 
                                dark 
                                flat 
                                class="purple darken-4" 
                                @click.native="onSaveChanges">
                                Save
                            </v-btn>
                        </template> 
                    </v-time-picker>
                </v-flex>
            </v-layout>
            
        </v-container>
    </v-card>
    </v-dialog>
</template>
<script>
export default {
    props:["meetup"],
    data(){
        return{
            editDialog:false,
            editableTime:null
        }
    },
    methods:{
        onSaveChanges(){
            const newDate = new Date(this.meetup.date) 
            const hours = this.editableTime.match(/^(\d+)/)[1];
            const minutes = this.editableTime.match(/:(\d+)/)[1];
            newDate.setHours(hours);
            newDate.setMinutes(minutes);
            this.$store.dispatch("updateMeetupData",{
                id:this.meetup.id,
                date:newDate
            })
        },

    },
    created(){
        this.editableTime = new Date(this.meetup.date)
    }
}
</script>
