<template>
  <v-app>
    <v-navigation-drawer temporary app v-model="sideNav">
        <v-list>
          <v-list-tile v-for="menu in menuItems" :key="menu.title"  router :to="menu.link">
            <v-list-tile-action>
              <v-icon>
                {{menu.icon}}
              </v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>{{menu.title}}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-list-tile  v-if="userIsAuth" @click="onLogout">
            <v-list-tile-action>
              <v-icon>
                exit_to_app
              </v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>Logout</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-navigation-drawer>
      <v-toolbar app color="primary" dark >
        <v-toolbar-side-icon @click.native.stop="sideNav = !sideNav"  class="hidden-sm-and-up"></v-toolbar-side-icon>
        <v-toolbar-title class="ml-0">
          <router-link to="/" tag="span" style="cursor:pointer">Meetups</router-link>
        </v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-items class="hidden-xs-only">
          <v-btn flat v-for="menu in menuItems" :key="menu.title" router :to="menu.link">
            <v-icon left>{{menu.icon}}</v-icon>
            {{menu.title}}
          </v-btn>
          <v-btn flat v-if="userIsAuth"  @click="onLogout">
            <v-icon left>exit_to_app</v-icon>
            Logout
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>
      <v-content>
        <router-view></router-view>
      </v-content>
  </v-app>
</template>

<script>

export default {
  name: 'App',
  components: {
  },
  data () {
    return {
      sideNav : false,
    }
  },
  computed:{
    menuItems(){
      let menuItems = [
        {icon:'face',title:'Sign Up',link:'/register'},
        {icon:'lock_open',title:'Sign In',link:'/signin'},
      ]
      if(this.userIsAuth){
        menuItems = [
          {icon:'supervisor_account',title:'View Meetups',link:'/meetups'},
          {icon:'room',title:'Organize Meetups',link:'/meetup/new'},
          {icon:'person',title:'Profile',link:'/profile'}
        ]
      }
      return menuItems
    },
    userIsAuth(){
      return this.$store.getters.user !== null && this.$store.getters.user !== undefined
    }
  },
  methods:{
     onLogout(){
       this.$store.dispatch("logout")
       this.$router.push('/signin')
     }
  }
}
</script>

<style>
.custom-loader {
    animation: loader 1s infinite;
    display: flex;
  }
  @-moz-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-o-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
</style>
