<template>
    <v-dialog
      persistent
      v-model="registerDialog"
    >
    <v-btn
      dark
      class="red darken-1 ml-0"
      accent
      slot="activator"
    >
    {{ userIsRegistered ? "Unregister": "Register" }}
    </v-btn>
    <v-card>
        <v-container>
            <v-layout row wrap>
                <v-flex xs12>
                    <v-card-title v-if="userIsRegistered">
                        Unregister from Meetup ?
                    </v-card-title>
                    <v-card-title v-else>
                        Register for Meetup ?
                    </v-card-title>
                </v-flex>
            </v-layout>
            <v-divider></v-divider>
            <v-layout row wrap>
                <v-flex xs12>
                    <v-card-text>
                        You Can change your descion later on...
                    </v-card-text>
                </v-flex>
            </v-layout>
            <v-layout row wrap>
                <v-flex xs12>
                    <v-card-actions>
                        <v-btn 
                        class="red--text darken-1" 
                        @click="registerDialog = false"
                        flat>
                            Cancel
                        </v-btn>
                        <v-btn 
                        class="green--text darken-1"
                        flat
                        @click="onAgree">
                            Conirm
                        </v-btn>
                    </v-card-actions>
                </v-flex>
            </v-layout>
        </v-container>
    </v-card>
    </v-dialog>
</template>
<script>
export default {
    props:["meetupId"],
    data(){
        return{
            registerDialog:false
        }
    },
    computed: {
        userIsRegistered(){
            return this.$store.getters.user.registeredMeetups.findIndex(meetupId => {
                return meetupId === this.meetupId
            }) >=0 
        },
    },
    methods:{
        onAgree(){
            if(this.userIsRegistered)
            {
                this.$store.dispatch("unRegisterUserForMeetup",this.meetupId)
            }
            else 
            {
                this.$store.dispatch("registerUserForMeetup",this.meetupId)
            }
        }
    }
}
</script>
