<template>
    <v-container>
        <v-layout row wrap v-if="loading"> 
            <v-flex  xs12 sm6 offset-sm3 class="text-xs-center">
                <v-progress-circular
                :size="70"
                :width="7"
                color="purple"
                indeterminate
                v-if="loading"
                ></v-progress-circular>
            </v-flex>
        </v-layout>
        <v-layout row v-else>
            <v-flex xs-12 md6 offset-md3>
                <v-layout row>
                    <v-flex xs12>
                        <v-card-title class="px-0">
                            <h3 class="display-1">
                                Profile 
                            </h3>
                            <template v-if="showUserDetails">
                                <v-spacer></v-spacer>
                                <editProfile-dialog :profile="getUserDetails"></editProfile-dialog>  
                            </template>
                        </v-card-title>
                    </v-flex>
                </v-layout>
                <template v-if="!showUserDetails">
                    <form @submit.prevent="onEdit">
                        <v-layout row>
                            <v-flex
                            xs12
                            >
                                <v-text-field
                                    type="text"
                                    v-model="firstName"
                                    label="First name"
                                    required
                                ></v-text-field>
                            </v-flex>
                        </v-layout>
                        <v-layout row>
                            <v-flex
                            xs12
                            >
                            <v-text-field
                                type="text"
                                v-model="lastName"
                                label="Last name"
                                required
                            ></v-text-field>
                            </v-flex>
                        </v-layout>
                        <v-layout row>
                            <v-flex
                            xs12
                            >
                            <v-text-field
                                type="email"
                                v-model="email"
                                label="Email"
                                required
                            ></v-text-field>
                            </v-flex>
                        </v-layout>
                        <v-layout row>
                            <v-flex
                            xs12 text-xs-right
                            >
                            <v-btn flat dark class="primary mr-0" type="submit" :loading="loading" :disabled="!formIsvalid">
                                Save
                                <span slot="loader" class="custom-loader">
                                    <v-icon light>cached</v-icon>
                                </span>
                            </v-btn>
                            </v-flex>
                        </v-layout>
                    </form>
                </template>
                <template v-else>
                   <v-layout row>
                       <v-flex>
                            <v-card-title primary-title>
                                <div>
                                    <div>First Name</div>
                                    <h3 class="headline mb-0"> {{ getUserDetails.firstname }} </h3>
                                </div>
                            </v-card-title>
                       </v-flex>
                   </v-layout>
                   <v-layout>
                       <v-flex row>
                           <v-divider></v-divider>
                       </v-flex>
                   </v-layout>
                   <v-layout row>
                       <v-flex>
                            <v-card-title primary-title>
                                <div>
                                    <div>Last Name</div>
                                    <h3 class="headline mb-0"> {{ getUserDetails.lastname }} </h3>
                                </div>
                            </v-card-title>
                       </v-flex>
                   </v-layout>
                   <v-layout>
                       <v-flex row>
                           <v-divider></v-divider>
                       </v-flex>
                   </v-layout>
                   <v-layout row>
                       <v-flex>
                            <v-card-title primary-title>
                                <div>
                                    <div>Email</div>
                                    <h3 class="headline mb-0"> {{ getUserDetails.email }} </h3>
                                </div>
                            </v-card-title>
                       </v-flex>
                   </v-layout>
                </template>
            </v-flex>
        </v-layout>
        
    </v-container>
</template>
<script>
export default {
    data(){
        return{
            firstName : "",
            lastName : "",
            email :""
        }
    },
    computed:{
        user(){
            return this.$store.getters.user
        },
        error(){
            return this.$store.getters.error
        },
        loading(){
            return this.$store.getters.loading
        },
        formIsvalid(){
            return this.firstName !== "" 
            && this.lastName !== ""
            && this.email !== ""
        },
        getUserDetails(){
            return this.$store.getters.userDetails
        },
        showUserDetails(){
            return this.getUserDetails !== null && this.getUserDetails !== undefined
        }
    },
    methods:{
        onEdit(){
            if(!this.formIsvalid)
            {
                return
            }
            const data = {
                firstName : this.firstName,
                lastName : this.lastName,
                email : this.email,
                user : this.user.id
            }
            this.$store.dispatch("onUserEdit",data)
        }
    }
}
</script>
