<template>
    <v-container>
        <v-layout row wrap v-if="loading"> 
            <v-flex  xs12 sm6 offset-sm3 class="text-xs-center">
                <v-progress-circular
                :size="70"
                :width="7"
                color="purple"
                indeterminate
                v-if="loading"
                ></v-progress-circular>
            </v-flex>
        </v-layout>
        <v-layout row wrap v-for="data in meetups" :key="data.id" class="mb-2" v-else>
            <v-flex xs-12 sm10 md8 offset-sm1 offset-md2>
                <v-card class="cyan lighten-4">
                    <v-container>
                        <v-layout row>
                            <v-flex xs5 sm4 md3>
                                <v-img
                                :src="data.imageUrl"
                                ></v-img>
                            </v-flex>
                            <v-flex xs7 sm8 md9>
                                <v-card-title primary-title>
                                    <div>
                                        <h3>{{data.title}}</h3>
                                        <div>{{data.date | date}}</div>
                                    </div>
                                </v-card-title>
                                <v-card-actions>
                                    <v-btn flat left dark class="cyan darken-2" v-bind:to="'/meetups/'+data.id">
                                        <v-icon>arrow_forward</v-icon>
                                        View Meetup
                                    </v-btn>
                                </v-card-actions>
                            </v-flex>
                        </v-layout>
                    </v-container>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>


<script>
export default {
    computed:{
        meetups(){
            return this.$store.getters.loadedMeetups
        },
        loading(){
            return this.$store.getters.loading
        }
    }
}
</script>
