<template>
    <v-dialog
      max-width="400px"
      persistent
      v-model="editProfile"
    >
        <v-btn
        color="primary"
        dark
        fab
        class="cyan darken-2"
        accent
        slot="activator"
        >
        <v-icon>edit</v-icon>
        </v-btn>
        <v-card>
            <v-container>
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-card-title>
                            <h1>
                                Edit Profile
                            </h1>
                        </v-card-title>
                    </v-flex>
                </v-layout>
                <v-divider></v-divider>
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-card-text>
                            <v-text-field
                                    name="firtname"
                                    label="First Name"
                                    id="firstname"
                                    v-model="firstname"
                                    required
                                >
                            </v-text-field>
                            <v-text-field
                                name="lastname"
                                label="Last Name"
                                id="lastname"
                                v-model="lastname"
                                required
                            >
                            </v-text-field>
                            <v-text-field
                                name="email"
                                label="Email"
                                id="email"
                                v-model="email"
                                required
                            >
                            </v-text-field>
                        </v-card-text>
                    </v-flex>
                </v-layout>
                <v-divider></v-divider>
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn dark flat class="purple darken-3" @click="editProfile = false">Close</v-btn>
                            <v-btn dark flat class="purple darken-4" @click="editOnSave">Save</v-btn>
                        </v-card-actions>
                    </v-flex>
                </v-layout>
            </v-container>
        </v-card>
    </v-dialog>
</template>
<script>
export default {
    props:['profile'],
    data(){
        return{
            firstname:this.profile.firstname,
            lastname:this.profile.lastname,
            email:this.profile.email,
            editProfile:false
        }
    },
    methods:{
        editOnSave(){
            const userData = {
                firstname: this.firstname,
                lastname:this.lastname,
                email:this.email,
                key:this.profile.key
            }
            console.log(userData)
            this.$store.dispatch("onUserUpdate",userData)
        }
    }
}
</script>
