import * as firebase from 'firebase'

export default{
    state:{
        user:null,
        userDetails:null
    },
    mutations:{
        registerUserForMeetup(state,payload)
        {
            const id = payload.id
            if(state.user.registeredMeetups.findIndex(meetup => meetup.id === id) >=0){
                return 
            }
            state.user.registeredMeetups.push(id)
            state.user.fbkeys[id] = payload.fbkey
        },  
        unRegisterUserForMeetup(state,payload){
            const registeredMeetups = state.user.registeredMeetups
            registeredMeetups.splice(registeredMeetups.findIndex(meetup => meetup.id === payload),1)
            Reflect.deleteProperty(state.user.fbkeys,payload)
        },
        setUser(state,payload){
            state.user = payload
        },
        saveUserDetails(state,payload)
        {
            state.userDetails = payload
        }
    },
    actions:{
        onUserUpdate({commit,getters},payload)
        {
            commit("setLoading",true)
            let updateObj = {}
            if(payload.firstname)
            {
                updateObj.firstname = payload.firstname
            }
            if(payload.lastname)
            {
                updateObj.lastname = payload.lastname
            }
            if(payload.email)
            {
                updateObj.email = payload.email
            }
            firebase.database().ref('/userDetails/'+getters.user.id).child(payload.key).update(updateObj)
                .then(() => {
                    commit("setLoading",false)
                    commit('saveUserDetails',payload)
                })
                .catch(error => {
                    console.log(error)
                    commit("setLoading",false)
                })
        },
        fetchUserDetails({commit,getters},payload)
        {
            commit('setLoading',true)
            let key,values,userData
            firebase.database().ref('/userDetails/'+getters.user.id).once('value')
                .then(data => {
                    values = data.val()
                    for(key in values)
                    {
                         userData = {
                            userKey: values[key].userKey,
                            firstname:values[key].firstname,
                            lastname:values[key].lastname,
                            email : values[key].email,
                            key:key
                        }
                    }
                    commit('setLoading',false)
                    commit('saveUserDetails',userData)
                })
                .catch(error => {
                    console.log(error) 
                    commit('setLoading',false)
                })
        },
        onUserEdit({commit,getters},payload)
        {
            commit('setLoading',true)
            let key
            const userData = {
                userKey: payload.user,
                firstname:payload.firstName,
                lastname:payload.lastName,
                email : payload.email,
            }
            firebase.database().ref("userDetails").child(getters.user.id).push(userData)
                .then(data =>{
                    commit('setLoading',false)
                    key = data.key
                    return key
                })
                .then(() => {
                    commit('saveUserDetails',{
                        ...userData,
                        id:key
                    })
                })
                .catch(error => {
                    console.log(error) 
                    commit('setLoading',false)
                })
        },
        registerUserForMeetup({commit,getters},payload){
            commit('setLoading',true)
            const user = getters.user
            firebase.database().ref('/users/'+user.id).child('/registrations/')
                .push(payload)
                .then((data) => {
                    commit('setLoading',false)
                    commit('registerUserForMeetup',{id:payload,fbkey: data.key})
                })
                .catch(error => {
                    console.log(error)
                    commit('setLoading',false)
                })
        },
        unRegisterUserForMeetup({commit,getters},payload){
            commit('setLoading',true)
            const user = getters.user
            if(!user.fbkeys)
            {
                return
            }
            const fbkey = user.fbkeys[payload]
            firebase.database().ref('/users/'+user.id+'/registrations/').child(fbkey).remove()
                .then(() => {
                    commit('setLoading',false)
                    commit('unRegisterUserForMeetup',payload)
                })
                .catch(error => {
                    console.log(error)
                    commit('setLoading',false)
                })
        },
        signUser({commit},payload){
            commit('setLoading',true)
            commit('clearError')
            firebase.auth().createUserWithEmailAndPassword(payload.email,payload.password)
                .then(
                    user => {
                        commit('setLoading',false)
                        const newUser = {
                            id: user.uid,
                            registeredMeetups:[],
                            fbkeys:{}
                        }
                        commit('setUser',newUser)
                    }
                )
                .catch(
                    error => {
                        commit('setLoading',false)
                        commit('setError',error)
                    }
                )
        },
        signUserIn({commit},payload){
            commit('setLoading',true)
            commit('clearError')
            firebase.auth().signInWithEmailAndPassword(payload.email,payload.password)
                .then(
                    user => {
                        commit('setLoading',false)
                        const newUser = {
                            id: user.uid,
                            registeredMeetups:[],
                            fbkeys:{}
                        }
                        commit('setUser',newUser)
                    }
                )
                .catch(error => {
                        commit('setLoading',false)
                        commit('setError',error)
                    }
                )
        },
        autoSignin({commit},payload)
        {
            commit('setUser',{
                id:payload.uid,
                registeredMeetups:[],
                fbkeys:{}
            })
        },
        fetchUserData({commit,getters}){
            commit('setLoading',true)
            firebase.database().ref('/users/'+getters.user.id+'/registrations/').once('value')
                .then(data => {
                    const values = data.val()
                    let registeredMeetups = []
                    let swappedPairs = {}
                    for(let key in values)
                    {
                        registeredMeetups.push(values[key])
                        swappedPairs[values[key]] = key
                    }
                    const updatedUser = {
                        id: getters.user.id,
                        registeredMeetups : registeredMeetups,
                        fbkeys : swappedPairs
                    }
                    commit('setLoading',false)
                    commit('setUser',updatedUser)
                })
                .catch(error => {
                        commit('setLoading',false)
                    }
                )
        },
        logout({commit}){
            firebase.auth().signOut()
            commit("setUser",null)
        }
    },
    getters:{
        user(state)
        {
            return state.user
        },
        userDetails(state)
        {
            return state.userDetails
        }
    }
}