<template>
    <v-dialog
      max-width="400px"
      persistent
      v-model="editDialog"
    >
    <v-btn
      fab
      color="primary"
      dark
      class="cyan darken-2"
      accent
      slot="activator"
    >
    <v-icon>edit</v-icon>
    </v-btn>
    <v-card>
        <v-container>
            <v-layout row wrap>
                <v-flex xs12>
                    <v-card-title>
                        Edit Meetup
                    </v-card-title>
                </v-flex>
            </v-layout>
            <v-divider></v-divider>
            <v-layout row wrap>
                <v-flex xs12>
                    <v-card-text>
                        <v-text-field
                                name="title"
                                label="Title"
                                id="title"
                                v-model="editedTitle"
                                required
                            >
                        </v-text-field>
                        <v-textarea
                            name="description"
                            label="Description"
                            id="description"
                            v-model="editedDesc"
                            required
                        >
                        </v-textarea>
                    </v-card-text>
                </v-flex>
            </v-layout>
            <v-divider></v-divider>
            <v-layout row wrap>
                <v-flex xs12>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn dark flat class="purple darken-3" @click="editDialog = false">Close</v-btn>
                        <v-btn dark flat class="purple darken-4" @click="editOnSave">Save</v-btn>
                    </v-card-actions>
                </v-flex>
            </v-layout>
        </v-container>
    </v-card>
    </v-dialog>
</template>

<script>
export default {
    props:['meetup'],
    data(){
        return{
            editDialog : false,
            editedTitle:this.meetup.title,
            editedDesc:this.meetup.description
        }
    },
    methods: {
        editOnSave() {
            if(this.editedTitle.trim() === '' || this.editedDesc.trim() === ''){
                return
            }
            this.editDialog = false
            this.$store.dispatch("updateMeetupData",{
                id:this.meetup.id,
                title:this.editedTitle,
                description:this.editedDesc
            })
        }
    }
}
</script>
