<template>
    <v-container>
        <v-layout row wrap v-if="loading"> 
            <v-flex  xs12 sm6 offset-sm3 class="text-xs-center">
                <v-progress-circular
                :size="70"
                :width="7"
                color="purple"
                indeterminate
                v-if="loading"
                ></v-progress-circular>
            </v-flex>
        </v-layout>
        <v-layout row wrap v-else>
            <v-flex xs12>
                <v-card>
                    <v-card-title>
                        <h4 class="display-1">{{meetup.title}}</h4>
                        <template v-if="userIsCreator">
                            <v-spacer></v-spacer>
                            <app-edit-meetup-dialog :meetup="meetup"></app-edit-meetup-dialog>
                        </template>
                    </v-card-title>
                    <v-img
                        :src="meetup.imageUrl" height="400px"
                    ></v-img>   
                    <v-card-text>
                        <div class="info--text">
                            {{meetup.date | date}} - {{meetup.location}}
                            <div><app-edit-meetup-date-dialog :meetup="meetup" v-if="userIsCreator"></app-edit-meetup-date-dialog><app-edit-meetup-time-dialog :meetup="meetup" v-if="userIsCreator"></app-edit-meetup-time-dialog></div>
                        </div>
                        <div>
                            {{meetup.description}}
                        </div>
                    </v-card-text> 
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <registration-dialog :meetupId="meetup.id" v-if="userIsAuthenticated && !userIsCreator"></registration-dialog>
                    </v-card-actions>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>
<script>
export default {
    props:['id'],
    computed:{
        meetup(){
            return this.$store.getters.loadedMeetup(this.id)
        },
        loading(){
            return this.$store.getters.loading
        },
        userIsAuthenticated(){
            return this.$store.getters.user !== null &&this.$store.getters.user !== undefined
        },
        userIsCreator(){
            if(!this.userIsAuthenticated)
            {
                return false
            }
            return this.$store.getters.user.id === this.meetup.creatorId
        }
    }
}
</script>

