<template>
    <v-container>
        <v-layout row v-if="error">
            <v-flex  xs12 sm6 offset-sm3>
                <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>  
            </v-flex>
        </v-layout>
        <v-layout>
            <v-flex xs12 sm6 offset-sm3>
                <v-card>
                    <v-card-text>
                        <v-container>
                            <form @submit.prevent="onSignup">
                                <v-layout row>
                                    <v-flex xs12>
                                        <v-text-field
                                            name="email"
                                            label="Email"
                                            id="email"
                                            v-model="email"
                                            type="email"
                                            required
                                        >
                                        </v-text-field>
                                    </v-flex>
                                </v-layout>
                                <v-layout>
                                    <v-flex xs12>
                                        <v-text-field
                                            name="password"
                                            label="password"
                                            id="password"
                                            v-model="password"
                                            type="password"
                                            required
                                        >
                                        </v-text-field>
                                    </v-flex>
                                </v-layout>
                                <v-layout>
                                    <v-flex xs12>
                                        <v-text-field
                                            name="confirmpassword"
                                            label="Confirm Password"
                                            id="confirmpassword"
                                            v-model="confirmpassword"
                                            type="password"
                                            required
                                            :rules="[comparePasswords]"
                                        >
                                        </v-text-field>
                                    </v-flex>
                                </v-layout>
                                <v-layout row>
                                    <v-flex xs12  text-xs-right>
                                        <v-btn flat dark 
                                        class="cyan darken-2 mx-0" 
                                        type="submit"
                                        :disabled="loading"
                                        :loading="loading"
                                        >
                                            Sign Up
                                            <span slot="loader" class="custom-loader">
                                                <v-icon light>cached</v-icon>
                                            </span>
                                        </v-btn>
                                    </v-flex>
                                </v-layout>
                            </form>
                        </v-container>
                    </v-card-text>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
export default {
    data(){
        return{
            email:"",
            password:"",
            confirmpassword:""
        }
    },
    computed:{
        comparePasswords(){
            return this.password !== this.confirmpassword ? 'Pass words does not match' : ""
        },
        user(){
            return this.$store.getters.user
        },
        error(){
            return this.$store.getters.error
        },
        loading(){
            return this.$store.getters.loading
        }
    },
    watch:{
        user(value)
        {
            if(value !== null && value !== undefined){
                this.$router.push('/')
            }
        }
    },
    methods:{
        onSignup(){
            this.$store.dispatch("signUser",{email:this.email,password:this.password})
        },
        onDismissed(){
            this.$store.dispatch("clearError")
        }
    }
}
</script>
