import 'babel-polyfill'
import Vue from 'vue'
import './plugins/vuetify'
import App from './App.vue'
import VueRouter from 'vue-router'
import Routes from './router/router'
import { store } from './store/store'
import DateFilter from  './filter/date'
import * as firebase from 'firebase'
import AlertCmp from './components/shared/Alert.vue'
import EditMeetupDialog from './components/Meetup/Edit/EditMeetupDialog.vue'
import EditMeetupDateDialog from './components/Meetup/Edit/EditMeetupDateDialog.vue'
import EditMeetupTimeDialog from './components/Meetup/Edit/EditMeetupTimeDialog.vue'
import RegisterDialog from './components/Meetup/Registration/RegisterDialog.vue'
import EditProfile from './components/User/Edit/EditProfile.vue'

Vue.config.productionTip = false
Vue.filter('date',DateFilter)
Vue.component("app-alert",AlertCmp)
Vue.component("app-edit-meetup-dialog",EditMeetupDialog)
Vue.component("app-edit-meetup-date-dialog",EditMeetupDateDialog)
Vue.component("app-edit-meetup-time-dialog",EditMeetupTimeDialog)
Vue.component("registration-dialog",RegisterDialog)
Vue.component("editProfile-dialog",EditProfile)
Vue.use(VueRouter)
const router = new VueRouter({
  routes: Routes,
  mode:'history'
});
new Vue({
  render: h => h(App),
  router:router,
  store,
  created(){
    firebase.initializeApp({
      apiKey: 'AIzaSyAf_ojtKMeol5UkmSTi96AttzPOjZohoPs',
      authDomain: 'meetups-28a5d.firebaseapp.com',
      databaseURL: 'https://meetups-28a5d.firebaseio.com',
      projectId: 'meetups-28a5d',
      storageBucket: 'meetups-28a5d.appspot.com',
      messagingSenderId: '269850632689'
    })
    firebase.auth().onAuthStateChanged( user => {
      if(user)
      {
        this.$store.dispatch("autoSignin",user)
        this.$store.dispatch("fetchUserData")
        this.$store.dispatch("fetchUserDetails")
      }
    })
    this.$store.dispatch("loadMeetups")
  }
}).$mount('#app')
